@import url('https://fonts.googleapis.com/css?family=Anton');


body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: -webkit-gradient(linear, left top, left bottom, from(#EDE574), to(#E1F5C4));
  background: -webkit-linear-gradient(#EDE574, #E1F5C4);
  background: -o-linear-gradient(#EDE574, #E1F5C4);
  background: linear-gradient(#EDE574, #E1F5C4);
  font-family: 'Anton', sans-serif;
}



